<template>
  <v-app style="max-width: 100%; height: 100%;">
    <router-view v-if="dataIsReady" style="max-width: 100%; height: 100%;"/>
  </v-app>
</template>

<script>

  import {mapActions} from 'vuex';

  export default {
    name: 'App',
    components: {},
    data() {
      return {
        dataIsReady: false
      }
    },
    methods: {
      ...mapActions('user', ['getRegistrationState', 'initState']),
      ...mapActions(['getLoggedUser'])
    },
    created() {
      let self = this;
      self.getLoggedUser().then(currentUser => {
        if (currentUser != null && currentUser != undefined) {
          // user is logged in
          let resultInitAppInfo = self.initState();

          resultInitAppInfo
                  .then(() => {
                    self.dataIsReady = true;
                  })
                  .catch((error) => {
                    self.snackbarText = error;
                    self.snackbar = true;
                  });
        } else {
          self.$store.dispatch('resetAppState').then(() => {
            self.$router.replace('home');
          })
                  .catch((error) => {
                    self.snackbarText = error;
                    self.snackbar = true;
                  });

          self.dataIsReady = true;
          console.log("No user logged");
        }
      });
      //this.checkRegistrationState();
      /*let self = this;
      let resultInitAppInfo = this.initAppInfo();

      resultInitAppInfo.then(() => {
          self.dataIsReady = true;
      })
          .catch((error) => {
              console.log(error)
          });*/

    }

  };
</script>

<style>
  #app-toolbar {
    width: 100%;
  }

  #loading {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    min-height: 100vh;
  }
</style>